<template>

    <div class="founders-signing-list">

        <div v-for="(founder, index) in founders"
             :key="founder.id"
             class="founders-signing-item">

            <div class="founders-signing-left">
                <div class="h6">{{founder.name}}</div>
                <div
                    v-if="founder.documents.length > 0"
                    class="founders-signing-documents">
                    <div
                        v-for="(document, index) in founder.documents"
                        :key="document.id"
                        class="founders-signing-document">
                        <div class="h6">{{index+1}}. {{ document.name }}:</div>
                        <div
                            v-for="file in document.files"
                            :key="file.id"
                            class="document-link"
                            @click="downloadFile(file)">
                            <span class="icon-document"></span>
                            <span class="document-link-main">
                                <span class="document-link-title">{{ file.name }}</span>
                                <span class="document-link-size">({{convertSize(file.size)}})</span>
                            </span>
                        </div>
                    </div>
                </div>
            </div>

            <div class="founders-signing-right">
                <div class="founders-item-status">

                    <div v-if="founder.sign.status != 1"
                         :class="['stick-no-bg', founderStatus(founder.sign.status).status]">
                        {{founderStatus(founder.sign.status).title}}
                    </div>

                    <template v-if="founder.sign.status !== 5">

                        <form v-if="!founder.entity"
                              class="founders-accept-form"
                              @submit.prevent="sendApproveMessage(index, 'phone')">
                            <input type="tel"
                                   ref="phone"
                                   class="form-control no-label"
                                   placeholder="Телефон"
                                   v-model="founder.phone"
                                   :class="validationClasses('phone', index)"
                                   v-mask="'+7 ### ### ## ##'"/>
                            <button :class="['btn btn-sm btn-border', {'loading': founder.load}]"
                                    :disabled="founder.load">Отправить
                            </button>
                        </form>

                        <form v-else
                              class="founders-accept-form"
                              @submit.prevent="sendApproveMessage(index, 'email')">
                            <input type="tel"
                                   class="form-control no-label"
                                   placeholder="Email"
                                   v-model="founder.email"
                                   :class="validationClasses('email', index)"/>
                            <button :class="['btn btn-sm btn-border', {'loading': founder.load}]"
                                    :disabled="founder.load">Отправить
                            </button>
                        </form>

                    </template>

                </div>
            </div>

        </div>

    </div>

</template>

<script>

    import {email, minLength, required} from "vuelidate/lib/validators";
    import {mapState} from "vuex";

    export default {
        name: "FoundersSigningList",
        validations: {
            founders: {
                required,
                $each: {
                    phone: {required, minLength: minLength(16)},
                    email: {required, email}
                }
            }
        },
        computed: {
            ...mapState({
                founders: state => state.founders.foundersFinish
            })
        },
        methods: {
            convertSize(size) {
                if ( size > 999 && size < 1000000) {
                    return `${size / 1000}КБ`
                }
                else if ( size > 999999 && 1000000000 ) {
                    return `${size / 1000000}МБ`
                }
                else {
                    return `${size}Б`
                }
            },
            founderStatus(id) {
                switch (id) {
                    case 2:
                        return {
                            title: 'Отправлено',
                            status: 'info'
                        }
                    case 3:
                        return {
                            title: 'Ожидание',
                            status: 'secondary'
                        }
                    case 4:
                        return {
                            title: 'Отклонено',
                            status: 'danger'
                        }
                    case 5:
                        return {
                            title: 'Согласовано',
                            status: 'success'
                        }
                    case 6:
                        return {
                            title: 'Ошибка',
                            status: 'danger'
                        }
                    default:
                        return {
                            title: '',
                            status: ''
                        }
                }
            },
            validationClasses(type, index) {
                if ( type == 'phone' ) {
                    return {invalid: this.$v.founders.$each.$iter[`${index}`].phone.$error ? (!this.$v.founders.$each.$iter[`${index}`].required || !this.$v.founders.$each.$iter[`${index}`].minLength) : false }
                }
                else if ( type == 'email' ) {
                    return {invalid: this.$v.founders.$each.$iter[`${index}`].email.$error ? (!this.$v.founders.$each.$iter[`${index}`].email.required || !this.$v.founders.$each.$iter[`${index}`].email.email) : false }
                }
            },
            sendApproveMessage(index, type) {
                let validItem = this.$v.founders.$each.$iter[`${index}`][type]
                if (validItem.$invalid) {
                    validItem.$touch();
                    return
                }
                type == 'phone' ? this.founders[index].phone = this.$options.filters.phoneFilter(this.founders[index].phone) : null
                this.$store.dispatch('sendMessageForFounderFinish', {id: this.$route.params.id, founder: this.founders[index]})
            },
            downloadFile (file) {
                this.$store.dispatch('getFile', file.id).then(blob => {
                    this.$store.dispatch('downloadFile', {content: blob, name: file.name, format: file.format})
                })
            }
        },
        mounted() {
            this.$store.dispatch('getFoundersFinish', this.$route.params.id)
        }
    }
</script>

