<template>

    <div class="signing-documents-container">

        <div class="go-back-link">
            <a href="#"
               @click="$router.go(-1)"><span class="icon-arrow-left"></span>Назад</a>
        </div>

        <div class="page-title">
            <h1>Подписание участниками сделки</h1>
            <p>Входящий договор получен 01.01.2021 8:39</p>
        </div>

        <div class="block">
            <div class="block-content">

                <div class="block-title">
                    <div class="h3"
                         v-html="$t('signing_documents.founders_title')" />
                    <p v-html="$t('signing_documents.founders_descr')" />
                </div>

                <FoundersSigningList :founders="founders" />

            </div>
        </div>

        <div class="block">
            <div class="block-content">

                <div class="block-title">
                    <div class="h3"
                         v-html="$t('signing_documents.contract_title')" />
                    <p v-html="$t('signing_documents.contract_descr')" />
                </div>

                <SigningComponentsList :documents="documentsForSigning" />

                <div class="signing-documents-list-submit">
                    <div class="signing-documents-list-submit-left">
                        <div class="need-approve">
                            <div class="h6">Для подписания кредитного договора необходимо чтобы <br>все учредители подписали договор поручительства</div>
                        </div>
                    </div>
                    <div class="signing-documents-list-submit-right">
                        <button class="btn"
                                :disabled="!canSigning"
                                @click="canSigningSubmit">Подписать
                        </button>
                    </div>
                </div>

            </div>
        </div>

        <Popup v-if="signingModal"
               ref="signingModal"
               @close-modal="signingModal = false">

            <div class="popup-title">
                <div class="h2">Подписание загруженных документов</div>
            </div>

            <div class="order-success">

                <div class="order-success-submit">
                    <button class="btn btn-block btn-lg"
                            :class="{'loading': load}"
                            :disabled="load"
                            @click="initDiadocScript">Подписать
                    </button>
                    <p>Подписание осуществляется при помощи ЭП</p>
                </div>

            </div>

        </Popup>

        <Popup v-if="sertificatesModal"
               ref="sertificatesModal"
               @close-modal="closeSertificatesModal"
               classes="sertificates-modal">

            <div class="popup-title">
                <div class="h2">Выберите необходимый сертификат для цифровой подписи документов:</div>
            </div>

            <div class="popup-body">
                <div class="select-sertificates">
                    <div v-for="sertificate in sertificates"
                         :key="sertificate._cadesCertificate.objid"
                         class="sertificates-item"
                         :class="{'active': selectedSertificates == sertificate}"
                         @click="selectedSertificates = sertificate">
                        <span>{{sertificate.name}} (действителен до: {{parseDate(sertificate.validTo)}})</span>
                    </div>
                </div>
                <button class="btn btn-block"
                        :disabled="signLoad"
                        :class="{'loading': signLoad}"
                        @click="createSignature">Подписать</button>
            </div>

        </Popup>

    </div>

</template>

<script>

import FoundersSigningList from "@/components/Blocks/FoundersSigningList";
import SigningComponentsList from "@/components/Blocks/SigningComponentsList"
import {mapState} from "vuex";
import Popup from "@/components/Blocks/Popup";
import {createAttachedSignature, createHash, getSystemInfo, getUserCertificates} from "crypto-pro";
import moment from "moment";

export default {
    name: "SigningDocuments",
    components: {SigningComponentsList, FoundersSigningList, Popup},
    computed: {
        ...mapState({
            company: state => state.users.company,
            documentsForSigning: state => state.createOrder.documentsForSigningFinish,
            founders: state => state.founders.foundersFinish
        }),
        canSigning() {
            let bool = true
            this.founders.filter(founder => founder.sign.status !== 5).length > 0 ? bool = false : null
            this.documentsForSigning.forEach(doc => {
                if (!doc.haveRead) {
                    bool = false
                }
            })
            return true
        }
    },
    data: () => ({
        signingSuccessModal: false,
        signingConfirmModal: false,
        rejectOrderConfirm: false,
        load: false,
        signingModal: false,
        diadocModal: false,
        errorDiadocModal: false,
        sertificatesModal: false,
        mobModal: false,
        signLoad: false,
        sertificates: [],
        selectedSertificates: {},
    }),
    methods: {
        canSigningSubmit() {
            if ( this.canSigning ) {
                this.signingModal = true
            }
        },
        closeConfirmModal() {
            this.rejectOrderConfirm = false
        },
        confirmOrder() {
            this.$refs.signingConfirmModal.closePopup()
            this.signingSuccessModal = true
        },
        rejectOrder() {
            this.$refs.signingConfirmModal.closePopup()
            this.rejectOrderConfirm = true
        },
        rejectOrderSuccess() {
            this.$router.push('/orders')
        },
        initDiadocScript() {
            if ( !this.load ) {
                this.load = true
                getSystemInfo().then(res => {
                    this.load = false
                    this.$refs.signingModal.closePopup()
                    getUserCertificates().then(sertList => {
                        if ( sertList.length > 0 ) {
                            let sertPromises = []
                            this.sertificates = []

                            sertList.forEach(sert => {
                                sertPromises.push(new Promise(resolve => {
                                    sert.isValid().then(() => {
                                        this.sertificates.push(sert)
                                        resolve();
                                    })
                                }))
                            })

                            Promise.all(sertPromises).then(() => {
                                this.load = false
                                this.$refs.signingModal.closePopup()
                                this.selectedSertificates = this.sertificates[0]
                                this.sertificatesModal = true
                            })
                        }
                    }).catch(res => {
                        switch (res) {
                            case 'certificates_not_found':
                                this.errorDiadocText = 'Не найдено ни одного сертификата.'
                                break;
                            case 'valid_certificates_not_found':
                                this.errorDiadocText = 'Не найдено ни одного валидного сертификата.'
                                break;
                            case 'certificate_read_error':
                                this.errorDiadocText = 'Ошибка чтения одного из сертификатов.'
                                break
                        }
                        this.errorDiadocModal = true
                    })
                }).catch(error => {
                    this.load = false
                    this.diadocModal = true
                    this.$refs.signingModal.closePopup()
                })
            }
        },
        readFile(messageFile) {
            return new Promise(function (resolve) {
                let fileReader = new FileReader();
                fileReader.onload = function () {
                    resolve(this.result);
                };
                fileReader.readAsArrayBuffer(messageFile);
            });
        },
        generateSignature(hash) {
            const sert = this.selectedSertificates
            let signaturePromise;
            return new Promise((resolve, reject) => {signaturePromise = createAttachedSignature(sert.thumbprint, hash)
                signaturePromise.then(function (signature) {
                    resolve(signature)
                }, function (error) {
                    this.errorDiadocText = error.message
                    this.errorDiadocModal = true
                    this.signLoad = false
                    reject()
                }).catch(error => {
                    this.errorDiadocText = error.message
                    this.errorDiadocModal = true
                    this.signLoad = false
                })
            })
        },
        createSignature() {
            this.signLoad = true
            let ths = this
            let signaturePromises = []
            let sendDataVisible = []
            this.documentsForSigning.forEach((finalDocsListItem, index) => {
                if ( finalDocsListItem.files.length !== 0 ) {
                    sendDataVisible.push({
                        id: finalDocsListItem.id,
                        docs: []
                    })
                    finalDocsListItem.files.forEach(file => {
                        signaturePromises.push(new Promise((resolve, reject) => {
                            this.$store.dispatch('getFile', file.id).then(resp => {
                                let fileObj = new File([resp], file.name, {type: file.format})
                                let messagePromise = this.readFile(fileObj)
                                messagePromise.then(message => {
                                    createHash(message).then(function() {
                                        ths.generateSignature(message).then(signature => {
                                            sendDataVisible[index].docs.push({
                                                id: file.id,
                                                signature
                                            })
                                            resolve()
                                        }).catch(() => {
                                            this.signLoad = false
                                        })
                                    }, function(error) {
                                        this.errorDiadocText = error.message
                                        this.errorDiadocModal = true
                                        this.signLoad = false
                                        reject()
                                    })
                                })
                            })
                        }))
                    })
                }
            })
            Promise.all(signaturePromises).then(() => {
                let finalPromises = []
                sendDataVisible.forEach(docs => {
                    finalPromises.push(new Promise(resolve => {
                        this.$store.dispatch('sendSignature', docs).then(res => {
                            this.signLoad = false
                            if (res) {
                                resolve()
                            }
                        })
                    }))
                })
                Promise.all(finalPromises).then(() => {
                    this.$store.dispatch('orderResultFinish', this.$route.params.id).then(resp => {
                        if ( resp ) {
                            this.signLoad = false
                            this.$refs.sertificatesModal.closePopup()
                            this.successModal = true
                            this.$router.push('/orders')
                        }
                        else {
                            this.signLoad = false
                        }
                    })
                }).catch(() => this.signLoad = false)
            })
        },
        closeSuccessModal() {
            this.successModal = false
            this.$router.push('/orders')
        },
        closeDiadocModal() {
            this.diadocModal = false
        },
        closeSertificatesModal() {
            this.sertificatesModal = false
        },
        parseDate(date) {
            return moment(date).format('DD.MM.YYYY')
        },
    },
    mounted() {
        this.$store.dispatch('getFoundersFinish', this.$route.params.id)
        this.$store.dispatch('getDocumentsForSigningFinish', this.$route.params.id)
    }
}
</script>

